import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { Tag } from './components'

class Tags extends React.Component{

	constructor(props) {
		super(props)

		this.addTag = this.addTag.bind(this)
		this.state = {
			isOn:false,
			tags:['Urgent', 'Internal','Design','Add Functionality' ]
		}

	}

	removeTag(e){
		e.preventDefault
		this.props.handleRemoveTag(e.target.value)
	}

	addTag(e) {
		e.preventDefault()
		this.props.handleAddTag(e.target.value)
	}

	render(){
		return(
			<div id="TagsForm">
				<form>
					lable:
					Add Tags
					<input type="text" onSubmit={this.addTag}> Tag Name </input>
				</form>
				<ul id="tags">
				{this.state.tags.map((tag,i) => {
					return (
						<li key={i}>
							<Tag />
						</li>
					)
				})}
				</ul>
			</div>
		)
	}

}

Tags.propTypes = {
	tags : PropTypes.object,
	handleAddTag: PropTypes.func,
	handleRemoveTag: PropTypes.func
}

export default connect(
	state => ({
		tags: state.tags
	}), {

	}
)(Tags)



